.cart_item {
    border-bottom: #f2f2f2 1px solid;
    position: relative;
    margin-bottom: 25px;
}

.cart-product {
    display: flex;
    align-items: center;
    padding: 10px 0;
}

.cart-product  .remove {
   margin-right: 10px;
}

.product-edit {
    padding: 10px 0;
    text-align: right;
}

@media screen and (max-width: 510px){
    .remove-product-button {
        position: absolute !important;
        left: -8px;
        top: -20px;
    }

    #product-quantity,
    #product-frame-type,
    #product-map-size{
        width: 100% !important;
    }

    .MuiFormControl-root.MuiTextField-root {
        width: 100% !important;
    }

    .MuiInputBase-root {
        width: 100% !important;
    }
}