#highlight {
    background: url(../../assets/images/brush-stroke-banner-6-1024x274-1.png);
    font-size: 1em;
    background-repeat: no-repeat;
    background-size: 100% 95%;
    padding: 5px 20px 5px 30px;
    color: #ffffff;
}

#banner-title {
    font-weight: bold;
}
html {
    font-size: 15px
}

#custom_cart button,
#custom_cart  input,
#custom_cart  textarea {
    border: none !important;
    height: unset;
}
#custom_cart  a:hover {
    color: #ffffff;
}
#custom_cart  .product:hover{
    box-shadow: unset;
}

#custom_cart .product .product-price:before {
    content: "";
}
#custom_cart  {
    margin-top: 50px;
    background-color: #fff;
    display: flex;
    /*flex-direction: column;*/
    align-items: center;
    justify-content: center;
    font-family: Roboto, sans-serif;
    font-weight: 400;
    font-size: calc(10px + 2vmin);
    box-sizing: unset !important;
}

#custom_cart .page-content {
    width: 100%;
    max-width: 1170px;
    padding-right: 20px;
    padding-left: 20px;
}

#custom_cart .text-center {
    text-align: center;
}

.banner {
    padding: 16px;
    text-align: center;
    font-size: 18px;
}

.cart-form-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 20px;
}

.empty-cart-from-wrapper {
    justify-content: center;
}

.cart-controls {
    margin: 10px 0;
    display: flex;
    justify-content: space-between;
    align-content: center;
    align-items: center;
}
.cart-form {
    width: 60%;
    margin-bottom: 60px;
}

@media screen and (max-width: 1150px){
    .cart-form-wrapper {
        flex-direction: column;
    }
    .cart-form {
        width: 100%;
    }
}

@media screen and (max-width: 720px){
    .cart-controls {
        flex-direction: column;
    }
    .cart-coupon {
        text-align: center;
        width: 100%;
    }
}

@media screen and (max-width: 510px){
    #custom_cart {
        margin-top: 30px !important;
    }
}
