
* {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-tap-highlight-color: transparent;
}

input, body, textarea, button, .expandingArea > pre, select {
    font-weight: 300;
    font-size: 5.5vw;
    font-family: 'Roboto', sans-serif;
    font-display: fallback;
}

input, textarea, select {
    -webkit-appearance: none;
    -webkit-border-radius: 0;
}

input[type=submit] {
    -webkit-border-radius: 0;
}

button {
    -webkit-border-radius: 0;
}

body, html {
    background-color: white;
    margin: 0;
    font-size: 4.5vw;
    overflow-x: hidden;
}

p {
    line-height: 1.6;
}

a {
    color: black;
}

h1, h2, h3 {
    font-weight: 300;
    margin: 0 0 0.6em 0;
}

h1 {
    font-size: 1.8rem;
}

h2 {
    font-size: 1.6rem;
    padding-top: 0.3em;
}

h3 {
    font-size: 1.4rem;
    padding-top: 0.3em;
}

#language-switch-banner {
    border: 0;
    color: black;
    font-size: 5.5vw;
    font-weight: 400;
    text-align: center;
    padding: 3vw;
    background: rgb(255,255,255);
    background: linear-gradient(0deg, rgba(255,255,255,1) 0%, rgba(244,244,244,1) 50%, rgba(218,227,255,1) 100%);
    border-bottom: 1px solid #aaa;
}

#language-switch-banner a {
    background: white;
    border: 0;
    color: #2c3a68;
    font-size: 5.5vw;
    border-radius: 5px;
    font-weight: 400;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.20);
    height: 15vw;
    width: 94vw;
    cursor: pointer;
    display: inline-block;
    box-sizing: border-box;
    text-decoration: none;
    text-align: center;
    line-height: 15vw;
    padding: 0 4vw;
}

#language-switch-banner a:active {
    transform: translateY(2px);
    box-shadow: none;
}

#language-switch-banner p {
    margin: 0;
    margin-bottom: 3vw;
}

.wrapper {
    position: relative;
}

.video-container {
    width: 100vw;
    height: 100vw;
    overflow: hidden;
    position: absolute;
}

.video-placeholder {
    width: 100vw;
    height: 100vw;
    top: 0;
    position: relative;
}

#video-cta {
    z-index: 100;
    position: absolute;
    background: #2c3a68;
    border: 0;
    color: white;
    font-size: 5.5vw;
    border-radius: 5px;
    font-weight: 400;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.20);
    bottom: -7.5vw;
    height: 15vw;
    left: 10vw;
    right: 10vw;
    width: 80vw;
    cursor: pointer;
    display: inline-block;
    box-sizing: border-box;
    text-decoration: none;
    text-align: center;
    line-height: 15vw;
    padding: 0 4vw;
}

#video-cta, .cta.animate .button {
    background-image: linear-gradient(45deg, #2c3a68, #2c3a68 45%, #6796b3 50%, #2c3a68 55%, #2c3a68);
    background-size: 300% 100%;
    background-position-x: 0;
    animation: shine 6s;
    animation-fill-mode: both;
    animation-iteration-count: infinite;
}

@keyframes shine {
    0% {
        background-position-x: 0;
    }
    88% {
        background-position-x: 0;
    }
    100%   {
        background-position-x: 100%;
    }
}

#video-cta:active {
    transform: translateY(2px);
    box-shadow: none;
}

.video-container #mobile-video, .video-container #desktop-video  {
    width: 100%;
    height: none;
}

.video-container #video-logo {
    width: 15vw;
    height: auto;
    position: absolute;
    top: 0;
    left: 0;
}

#desktop-video-container {
    display: none;
}

#mobile-video, #surprise-video {
    display: block;
}

#post-video-content {
    position: relative;
    background: white;
    padding-top: 7.5vw;
    z-index: 2;
}

#examples-section {
    position: relative;
}

.examples {
    justify-content: flex-start;
    flex-wrap: nowrap;
    display: flex;
    overflow-y: scroll;
    padding: 10px 0;
    align-items: flex-start;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
}

.example-paginator {
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    width: 70px;
    background-size: 50px 50px;
    background-repeat: no-repeat;
    background-position: center center;
    margin-top: 40px;
}

.example-paginator:hover {
    opacity: 0.7;
    cursor: pointer;
}

#example-paginator-left {
    left: 0;
    background-image: url(/assets/img/what/arrow-left.svg);
}

#example-paginator-right {
    right: 0;
    background-image: url(/assets/img/what/arrow-right.svg);
}

.example {
