}

.insta-more a {
    text-decoration: none;
    position: absolute;
    left: 0;
    right: 0;
    text-align: center;
}

.examples .padding {
    width: 10vw;
    height: 10px;
    flex-shrink: 0;
}

.insta-more a:before {
    content: '';
