@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes growin {
    from {
        transform: scale(0.9);
        opacity: 0;
    }
    to   {
        transform: scale(1);
        opacity: 1;
    }
}

.social-proof.pre-animate {
    transform: scale(0.9);
    opacity: 0;
}

.social-proof.animate {
    animation: growin 1.5s;
    animation-fill-mode: both;
}

.pre-animate .example, .pre-animate .highlight {
    opacity: 0;
}

.animate .example, .animate .highlight {
    animation: fadein 1.5s;
    animation-fill-mode: both;
}

.animate .example:nth-of-type(2), .animate .highlight:nth-of-type(2) {
    animation-delay: 0.2s
}

.animate .example:nth-of-type(3), .animate .highlight:nth-of-type(3) {
    animation-delay: 0.4s
}

.animate .example:nth-of-type(4), .animate .highlight:nth-of-type(4) {
    animation-delay: 0.6s
}

.animate .example:nth-of-type(5), .animate .highlight:nth-of-type(5) {
    animation-delay: 0.8s
}

.animate .example:nth-of-type(6), .animate .highlight:nth-of-type(6) {
    animation-delay: 1.0s
}

.animate .example:nth-of-type(7), .animate .highlight:nth-of-type(7) {
    animation-delay: 1.2s
}

.animate .example:nth-of-type(8), .animate .highlight:nth-of-type(8) {
    animation-delay: 1.4s
}

.animate .example:nth-of-type(9), .animate .highlight:nth-of-type(9) {
    animation-delay: 1.6s
}

.animate .example:nth-of-type(10), .animate .highlight:nth-of-type(10) {
    animation-delay: 1.8s
}

.designs img.pre-animate, .love-letters img.pre-animate, .review.pre-animate {
    opacity: 0;
}

.designs img.animate, .love-letters img.animate, .review.animate {
    animation: fadein 1.5s;
    animation-fill-mode: both;
}

