.cart-totals-wrapper {
    width: 35%;
}

.cart-totals,
.checkout-payment {
    border: 1px solid #e2e2e2;
    border-bottom: none;
    background-color: #f9f9f9;
    font-size: 20px;
}

.cart-totals__items {
    display: flex;
    justify-content: space-between;
    padding: 15px;
    border-bottom: 1px solid #e2e2e2;
}

.shipping {
    margin: 13px 0;
    text-align: left;
}

.proceed-to-checkout {
    margin: 20px 0;
}

@media screen and (max-width: 1150px){
    .cart-totals-wrapper {
        width: 100%;
    }
}

.item-value {
    color: darkgreen;
}

.StripeElement {
    background-color: #f9f9f9 !important;
}